<template>
    <div>
            BaseUserEntity info
        <div class="user-info">
            <template v-if="user">
                <ul>
                    <li>My name: {{user.name}}</li>
                    <li>My surname: {{user.surname}}</li>
                    <li>My login: {{user.login}}</li>
                    <li>My organization(school/college/university): {{user.organization}}</li>
                    <li>My email: {{user.email}}</li>
                </ul>
            </template>
            <template v-else>
                <div>No user found!</div>
            </template>
        </div>
    </div>
</template>


<script>
    export default {
        props: ['user'],
        name: "My profile"
    }
</script>

