<template>
    <div class="middle">
        <aside>
            <SidebarPost v-for="post in viewPosts" :post="post" :key="post.id"/>
        </aside>
        <main>
            <Index v-if="page === 'Index'"/>
            <Enter v-if="page === 'Enter'"/>
            <Register v-if="page === 'Register'"/>
            <MyProfile v-if="page === 'MyProfile'" :user="user"/>
            <UploadSolution v-if="page === 'UploadSolution'" :user="user"/>
            <MySolutions v-if="page === 'MySolutions'" :user="user"/>
        </main>
    </div>
</template>
<script>
    import Index from './middle/Index';
    import Enter from './middle/Enter';
    import Register from './middle/Register';
    import MyProfile from './middle/MyProfile';
    import UploadSolution from './middle/UploadSolution';
    import SidebarPost from './SidebarPost';
    import MySolutions from './middle/MySolutions';

    export default {
        name: "Middle",
        props: ["posts", "user"],
        data: function () {
            return {
                page: "Index"
            }
        },
        computed: {
            viewPosts: function () {
                return Object.values(this.posts).sort((a, b) => b.id - a.id).slice(0, 2);
            }
        },
        components: {
            Index,
            Enter,
            Register,
            MyProfile,
            UploadSolution,
            MySolutions,
            SidebarPost
        }, beforeCreate() {
            this.$root.$on("onChangePage", (page) => {
                this.page = page;
            });
        }
    }
</script>

